		if (type !== 'complex1') {
			$('.js-s3d-filter').removeClass('active')
			// $('.js-s3d-filter__show').css('display','none');
			// return;
		} else if (type !== 'apart') {
			// $('.js-s3d-filter__open').css('display','flex');
			// $('.js-s3d-filter__show').css('display','flex');
			// return;
		}
		// $('.js-s3d-filter').show();
